<template>
<transition name="translate">
  <div id="drawer" v-if="visible">
    <div class="mask">
      <transition name="translate">
        <div class="content" :style="{ width: `${width}px` }" >
          <div class="top">
            <div class="left">
              <slot name="left">
                <div @click="close" style="cursor: pointer;">
                  <span>&lt; </span>
                  <span>返回</span>
                </div>
              </slot>
            </div>
            <div class="center">
              <slot name="center">
                <span>{{ title }}</span>
              </slot>
            </div>
            <div class="right">
              <slot name="right">
                <span @click="confirm" style="cursor: pointer;">保存</span>
              </slot>
            </div>
          </div>
          <div class="wapper">
            <slot></slot>
          </div>
        </div>
      </transition>
    </div>
  </div>
</transition>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      default: 450,
    },
    title: {
      type: String,
      default: "信息",
    },
    visible: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    close() {
      this.$emit("close");
    },
    confirm() {
      this.$emit('confirm')
    }
  },
};
</script>

<style lang="less" scoped>
#drawer {
  position: absolute;
  top: 58px;
  left: 0;
  width: 100%;
  height: calc(100vh - 58px);
  .mask {
    width: inherit;
    height: inherit;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .content {
    background-color: #fff;
    height: 84.5%;
    float: right;
    .top {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 44px;
      background-color: #f7f7f7;
      box-sizing: border-box;
      div {
        flex: 1;
        padding: 12px;
      }
      .left {
        margin-top: 4px;
        span:first-child {
          font-weight: bold;
          color: #3a6df0;
        }
      }
      .center {
        font-size: 18px;
        text-align: center;
        font-weight: bold;
      }
      .right {
        margin-top: 4px;
        text-align: right;
        color: #3a6df0;
      }
    }
  }
}
.translate-enter-active, .translate-leave-active {
  transition: all .3s ease-in-out;
}
.translate-enter, .translate-leave-to {
  transform: translateX(450px);
}
</style>
