<template>
  <div class="content-wrapper_svg">
    <div class="title">
      <div>
        <span>{{ pName }}</span>
        <!-- <button class="btn-cli" :data-clipboard-text="pName" @click="copyMsg">123</button> -->
        <sup class="count">{{svgList.length}}</sup>
        <!-- <span @click="editInfo" v-if="uid == author">
          <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1093"><path d="M684.202667 117.248c15.893333-15.872 42.154667-15.36 58.922666 1.408l90.517334 90.517333c16.661333 16.661333 17.344 42.986667 1.429333 58.922667l-445.653333 445.653333c-7.936 7.914667-23.104 16.746667-34.218667 19.776l-143.701333 39.253334c-21.909333 5.994667-35.114667-7.104-29.568-28.949334l37.248-146.773333c2.773333-10.944 11.562667-26.346667 19.392-34.176l445.653333-445.653333zM268.736 593.066667c-2.901333 2.901333-8.106667 12.074667-9.130667 16.021333l-29.12 114.773333 111.957334-30.570666c4.437333-1.216 13.632-6.549333 16.810666-9.728l445.653334-445.653334-90.517334-90.496-445.653333 445.653334zM682.794667 178.986667l90.517333 90.517333-30.186667 30.186667-90.496-90.517334 30.165334-30.165333z m-362.026667 362.048l90.496 90.517333-30.165333 30.165333-90.517334-90.496 30.165334-30.186666zM170.666667 874.666667c0-11.776 9.429333-21.333333 21.461333-21.333334h661.077333a21.333333 21.333333 0 1 1 0 42.666667H192.128A21.333333 21.333333 0 0 1 170.666667 874.666667z" p-id="1094"></path></svg>
        </span> -->
      </div>
      <div>
        <span @click="likeBtn"><img src="@/assets/img/like.svg" alt="" width="25" height="25"> {{likeNum}}</span>
        <span @click="addColl"><img src="@/assets/img/collections.svg" alt="" width="25" height="25"> {{collNum}}</span>
      </div>
    </div>
    <div class="tag">
      <span v-for="(item, index) in tagList" :key="index">
        {{item}}
      </span>
    </div>
    <div class="description">
      <div class="author_wrapper">
        <img :src="avatarUri" alt="" width="30" height="30">
        <span>{{author}}: <span>{{description?description:'这个人很懒，什么都没有说'}}</span></span>
      </div>
    </div>
    <div class="svg_list">
      <ul>
        <li v-for="item in svgList" :key="item.id">
          <div class="shadow"></div>
          <div class="svg_name">{{item.svg_name}}</div>
          <div class="hover_over">
            <span>
              <img src="@/assets/img/copy.svg" :class="'btn_'+item.id" :data-clipboard-text="item.svg_code" alt="" width="25" height="25"  @click="copyMsg(item.id)">
            </span>
            <span>
              <img src="@/assets/img/download.svg" alt="" width="22" height="22" @click="download(item.id)">
            </span>
            <span @click="del(item.id, item.svg_name)" v-if="uid ==item.author">
              <img src="@/assets/img/trash.svg" alt="" width="22" height="22">
            </span>
            <span v-else>
              <img src="@/assets/img/collections_1.svg" alt="" width="22" height="22">
            </span>
          </div>
        </li>
        <li v-if="uid == authorId" @click="openDrawer" style="align-items: center;">
          <span style="font-size: 45px">+</span>
        </li>
      </ul>
    </div>
    <!-- <modal :title="'编辑图包'" :visible="visible" @cancel="cancel" @confirm="confirm">
      <Row class="edit_row">
        <Col span="6" >图包名称</Col>
        <Col span="18"><Input v-model="edit.name" placeholder="请输入..." :maxlength="16"></Input></Col>
      </Row>
      <Row class="edit_row taglist">
        <Col span="6">图包标签</Col>
        <Col span="18">
          <span v-for="(item, index) in edit.tagList" :key="index" class="tag">
            <span>{{item}} <span @click="delTag(index)" style="cursor: pointer">x</span></span>
          </span>
          <span style="cursor: pointer" @click="addTag" class="tag">
            <span>自定义 +</span>
          </span>
        </Col>
      </Row>
      <Row class="edit_row">
        <Col span="6">图包描述</Col>
        <Col span="18"><Input v-model="edit.description" type="textarea" :autosize="{ minRows: 6, maxRows: 6 }" placeholder="请输入..." :maxlength="64" resize="none"></Input></Col>
      </Row>
    </modal> -->
    <!-- <modal :title="'自定义标签'" :visible="tagVisible" @cancel="tagCancel" @confirm="tagConfirm">
      <Input v-model="newTag" placeholder="请输入..." :maxlength="4"></Input>
    </modal> -->
    <drawer :visible="addSvgVisible" :title="'新增'" class="drawer" @close="close" @confirm="addSvgConfirm">
      <div class="data_wrapper">
        <ul>
          <li v-for="(item, index) in fileList" :key="index">
            <div v-html="item.svg_code" class="svg_wrapper"></div>
            <!-- <div>{{item.svg_name}}</div> -->
            <div class="del" @click="delSvg(index)">x</div>
          </li>
        </ul>
        <div>
          <label for="upload" style="width: 100%;height: 100%;display: flex;justify-content: center;align-items: center;cursor: pointer;">
            <span>+</span>
            <input type="file" id="upload" multiple>
          </label>
        </div>
      </div>
      <div class="info">
        <Row class="info_item">
          <Col span="6" align="left" class="key">图包名称</Col>
          <Col span="18" align="right" class="val">{{pName}}</Col>
        </Row>
        <Row class="info_item">
          <Col span="6" align="left" class="key">图包作者</Col>
          <Col span="18" align="right" class="val">{{author}}</Col>
        </Row>
        <Row class="info_item">
          <Col span="6" align="left" class="key">图包标签</Col>
          <Col span="18" align="right" class="val">
            <div class="tag">
              <span v-for="(item, index) in tagList" :key="index">
                {{item}}
              </span>
            </div>
          </Col>
        </Row>
        <div class="line"></div>
        <Row class="info_item">
          <Col span="6" align="left" class="key">图包描述</Col>
          <Col span="18" align="right" class="val description">{{description}}</Col>
        </Row>
       <div class="line"></div>
        <Row class="info_item">
          <Col span="6" align="left" class="key">总赞数</Col>
          <Col span="18" align="right" class="val">{{likeNum}}</Col>
        </Row>
        <Row class="info_item">
          <Col span="6" align="left" class="key">收藏数</Col>
          <Col span="18" align="right" class="val">{{pName}}</Col>
        </Row>
        <Row class="info_item">
          <Col span="6" align="left" class="key">创建日期</Col>
          <Col span="18" align="right" class="val">{{date}}</Col>
        </Row>
      </div>
    </drawer>
    <modal :title="'操作提示'" :visible="delVisible" @cancel="delCancel" @confirm="delConfirm">
      <span>确定删除{{delSvgName}}</span>
    </modal>
  </div>
</template>

<script>
import Clipboard from 'clipboard'
import { getSvgPackageInfo, updateSvgPackageInfo, getSvgList, uploadSvg, deleteSvg } from "@/network/svg/index";
import {like, addUserCollections} from "@/network/operation/index" 
import Modal from '@/components/Modal.vue'
import Drawer from '@/components/Drawer.vue'
import {shadow} from  '@/common/utils/utils'

export default {
  name: 'svglist',
  data() {
    return {
      uid: '',
      svgList: [],
      pid: "",
      likeNum: 0,
      collNum: 0,
      pName: "",
      avatarUri: '',
      description: "",
      authorId: '',
      author: '',
      tagList: [],
      date: '',
      visible: false,
      edit: {
        name: '',
        tagList: [],
        description: ''
      },
      tagVisible: false,
      newTag: '',
      addSvgVisible: false,
      fileList: [],
      delVisible: false,
      delPid: '',
      delSvgName: '',
    };
  },
  components: {
    Modal,
    Drawer,
    Clipboard
  },
  created() {
    this.init(this.$route.params.id)
    this.$EventBus.$on("getPid", (pid) => {
      this.init(pid);
    });
    this.openFile()
  },
  mounted() {

  },
  watch: {
    svgList: function(newList) {
      this.$nextTick(() => {
        const els = document.querySelectorAll(`.shadow`)
        const width = 35
        const height = 35
        shadow(els, this.svgList, width, height)
      }) 
    }
  },
  deactivated() {
    // this.svgList = []
  },
  methods: {
    // 初始化
    init(pid) {
      if (!pid) {
        this.$router.push({name: 'index'})
        return 
      }
      this.pid = pid;
      if (JSON.parse(localStorage.getItem('userInfo'))) {
        this.uid = JSON.parse(localStorage.getItem('userInfo')).id
      }
      this.getPackage();
      this.getList();
    },
    // 获取图包信息
    getPackage() {
      getSvgPackageInfo(this.pid).then(res => {
        if (res.status == 200) {
          const data = res.data[0]
          this.pName = data.package_name
          this.description = data.package_description
          this.avatarUri = data.avatar
          this.likeNum = data.like_num
          this.collNum = data.coll_num
          this.authorId = data.author
          this.author = data.author_name
          this.tagList = data.package_tag.split(' ')
          this.date = data.date
        } else {
          this.$toast.show("请求失败，请重试");
        }
      }).catch(err => {
        this.$toast.show("请求错误，请重试");
      })
    },
    // 获取svg列表
    getList() {
      getSvgList(this.pid)
        .then((res) => {
          if (res.status == 200) {
            this.svgList = res.data;
          } else {
            this.$toast.show("请求失败，请重试");
          }
        })
        .catch((err) => {
          this.$toast.show("请求错误，请重试");
        });
    },
    // 点赞
    likeBtn() {
      if(this.uid == '') {
        this.$toast.show('请登录')
        return
      }
      like(this.pid, this.uid)
        .then((res) => {
          if (res.status == 200) {
            this.likeNum += res.data.result;
            if (this.likeNum == 1) {
              this.$toast.show('感谢推荐')
            } else {
              this.$toast.show('取消点赞')
            }
          }
        })
        .catch((err) => {
          this.$toast.show(err)
        });
    },
    // 收藏
    addColl() {
      if(this.uid == '') {
        this.$toast.show('请登录')
        return
      }
      addUserCollections(this.pName, this.uid, this.pid).then((res) => {
          if (res.status == 200) {
            this.collNum += res.data.result;
            if (this.collNum == 1) {
              this.$toast.show('感谢收藏')
            } else {
              this.$toast.show('取消收藏')
            }
          } else if (res.status == 201) {
            this.$toast.show('感谢收藏')
            this.collNum += 1
          }
        })
        .catch((err) => {
          this.$toast.show(err)
        });
    },
    // 删除svg
    del(id, svg_name) {
      this.delPid = id
      this.delSvgName = svg_name
      this.delVisible = true
    },
    delCancel() {
      this.delVisible = false
    },
    delConfirm() {
      deleteSvg(this.delPid).then(res => {
        this.delVisible = false
        if (res.status == 200) {
          this.$toast.show(res.data.message || '删除成功');
          this.getList();
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    download(id) {
      const a = document.createElement("a");
      const url = `http://127.0.0.1:8000/api/svg/download/?pid=${id}`;
      a.href = url;
      a.download = "123.svg";
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);
    },
    // 新增图片
    openFile() {
      const el = document.getElementById('app')
      el.addEventListener('change', e => {
        const data = e.target.files
        for (let item of data) {
          if (item.type != 'image/svg+xml' && item.type != 'text/html') {
            return
          }
        }
        for (let item of data) {
          const reader = new FileReader();
          reader.readAsText(item, 'UTF-8')
          reader.onload = (evt) => {
            this.fileList.push({
              pid: this.pid,
              svg_name: item.name.split('.')[0],
              svg_code: evt.target.result,
              pure_svg: item.type == 'image/svg+xml'? true: false,
            })
          }
        }
      })
    },
    openDrawer() {
      this.addSvgVisible = true
      this.fileList = []
    },
    close() {
      this.addSvgVisible = false
    },
    addSvgConfirm() {
      if (this.fileList.length == 0) {
        return
      }
      this.fileList.forEach(item => {
        const {pid, svg_name, svg_code, pure_svg} = item
        uploadSvg(pid, this.uid, svg_name, svg_code, pure_svg).then(res => {
          if (res.status == 201) {
            this.$toast.show('上传成功')
            this.getList();
            this.addSvgVisible = false
          } else {
            this.$toast.show('请求失败，请重试')
          }
        }).catch(err => {
          this.$toast.show(err.response.data.message || "请求错误，请重试");
        })
      })
    },
    delSvg(index) {
      this.fileList.splice(index, 1)
    },
    // 复制文本
     copyMsg(id) {
      const clipboard = new Clipboard(`.btn_${id}`)
      clipboard.on('success', (e) => {
        this.$toast.show('复制成功')
        e.clearSelection();
      })
      clipboard.on('error', () => {
        this.$toast.show('复制失败')
      })
    }
  },
};
</script>

<style lang="less" scoped>
.content-wrapper_svg {
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding: 20px 40px;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--theme-bg-color);
  @media screen and (max-width: 510px) {
    padding: 20px;
  }
  &.overlay {
    pointer-events: none;
    transition: 0.3s;
    background-color: var(--overlay-bg);
  }
}
.title {
  display: flex;
  justify-content: space-between;
  font-size: 32px;
  margin: 12px 0;
  >div:last-child {
    font-size: 16px;
    margin-top: 10px;
    span {
      margin-left: 40px;
    }
  }
  .count {
    font-size: 14px;
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    line-height: 20px;
    margin-left: 8px;
    border-radius: 50%;
    background-color: #3a6df0;
    color: #fff;
  }
  svg {
    fill: var(--theme-color);
    cursor: pointer;
  }
}
.tag {
  margin-bottom: 12px;
  font-size: 14px;
  >span {
    background-color: #f1f2f3;
    color: #777;
    margin-right: 12px;
    padding: 4px 8px;
    border-radius: 30px;
  }
}
.description {
  display: flex;
  width: 100%;
  color: #888;
  .author_wrapper{
    display: flex;
    width: 100%;
    margin-bottom: 12px;
    margin-right: 18px;
    span {
      line-height: 30px;
      font-size: 20px;
      font-weight: bold;
      margin-left: 4px;
      >span {
        font-size: 15px;
        font-weight: normal;
        line-height: 32px;
        text-decoration: underline;
      }
    }
  }
}
.svg_list {
  width: 100%;
  height: 100vh;
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      display: flex;
      justify-content: center;
      list-style: none;
      position: relative;
      float: left;
      margin: 15px 13px;
      width: 105px;
      height: 115px;
      text-align: center;
      position: relative;
      overflow: visible;
      border: 1px dashed transparent;
      border-radius: 5px;
      background-color: rgba(255, 255, 255, 0.2);
      transition: all 0.3s ease-in-out;
      cursor: pointer;
      .shadow {
        padding-top: 18px;
      }
      &:hover {
        transform: translateY(-8px) scale(1.1);
        box-shadow: 0 0 5px #eee;
      }
      .svg_name {
        width: 100%;
        position: absolute;
        bottom: 4px;
        padding: 0 4px;
        white-space: nowrap; 
        overflow: hidden; 
        text-overflow: ellipsis;
      }
      .hover_over {
        position: absolute;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(13, 10, 49, 0.8);
        border-radius: 5px;
        opacity: 0;
        transition: all 0.2s ease-in;
        color: #fff;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}
.edit {
  margin-top: 50%;
}
.edit_row {
  font-size: 14px!important;
  margin: 10px 0;
}
.taglist {
  display: flex;
  flex-wrap: wrap;
}
.drawer {
  .data_wrapper {
    padding: 30px;
    max-height: 200px;
    overflow: auto;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 70px;
        margin-right: 12px;
        margin-bottom: 12px;
        border: 1px solid #ccc;
        border-radius: 5px;
        list-style: none;
        font-size: 12px;
        .del {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          top: -5px;
          right: -5px;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          color: #000;
          background-color: #ccc;
        }
      }
    }
    > div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 70px;
      height: 70px;
      border: 1px solid #ccc;
      border-radius: 3px;
      font-size: 20px;
      cursor: pointer;
      span {
        cursor: pointer;
      }
      input {
        display: none;
      }
    }
  }
  .info {
    padding: 12px 30px;
    font-size: 18px;
    .key {
      font-weight: bold;
    }
    .description {
      font-size: 16px;
      border-bottom: none;
    }
    .info_item {
      margin: 12px 0;
    }

    .tag {
      margin: 0;
      padding-top: 5px;
      span:last-child {
        margin-right: 0;
      }
    }
  }
}
.line {
  width: 100%;
  margin: 18px 0;
  border-bottom: 1px solid #ccc;
}
.svg_wrapper {
  width: 35px;
  height: 35px;
}
</style>
